import { Component } from "react";
import style from './Region.module.css';
import { HiOutlineRefresh, HiPlus } from 'react-icons/hi';

type Props = {
    onRefresh: () => void,
    onCreate:() => void
}

class RegionsControlPanel extends Component<Props> {
    render() {
        return (
            <div className={style.controlPanel}>
                <HiOutlineRefresh onClick={this.props.onRefresh}/>
                <HiPlus onClick={this.props.onCreate} />
            </div>
        );
    }
}

export default RegionsControlPanel;