import { Component } from "react";
import style from './Region.module.css';
import Region from "./Region";
import { RegionInfo } from "../../store/types/region/RegionInfo";
import { HiPlus } from 'react-icons/hi';
import { HiOutlineRefresh } from 'react-icons/hi';
import { AddRegionRequest } from "../../store/types/region/AddRegionRequest";
import { EditRegionRequest } from "../../store/types/region/EditRegionRequest";
import RegionControlPanel from "./RegionControlPanel";
import RegionHeader from "./RegionHeader";
import RegionsControlPanel from "./RegionsControlPanel";

type ComponentProps = {
    regions: Array<RegionInfo>,
    createRegion: (region: AddRegionRequest) => void,
    getRegions: (skip: number, take: number) => void,
    editRegion: (id: number, region: EditRegionRequest) => void,
    deleteRegion: (id: number) => void
}

class Regions extends Component<ComponentProps> {
    state = {
        isAddMode: false,
        isEditMode: false,
        editRegion: { id: 0, name: "", code: "" } as RegionInfo
    }

    componentDidMount(){
        this.getRegions();
    }

    getRegions = () => {
        this.props.getRegions(0, 10);
    }

    addNewRegion = (newRegion: AddRegionRequest) => {
        this.props.createRegion(newRegion);
        this.toggleAddMode();
    }
    
    editRegion = (editedRegion: EditRegionRequest) => {
        const id = this.state.editRegion.id;
        this.props.editRegion(id, editedRegion);
        this.closeEditMode();
    }

    deleteRegion = (id: number) => {
        if (this.state.editRegion.id === id)
            this.closeEditMode();

        this.props.deleteRegion(id);
    }

    openEditMode = (region: RegionInfo) => {
        this.setState({
            isAddMode: false,
            isEditMode: true,
            editRegion: region
        });
    }

    closeEditMode = () => { 
        this.setState({ 
            isEditMode: false, 
            editRegion: { id: 0, name: "", code: "" } as RegionInfo
        }); 
    }

    toggleAddMode = () => {
        this.setState({ 
            isAddMode: !this.state.isAddMode 
        }); 
    }

    render() {
        // console.log(this.props);
        return (
            <div className={style.regionsContainer}>
                <RegionHeader />

                { this.props.regions.map(region => <Region key={region.id} region={region} openEditMode={this.openEditMode} deleteRegion={this.deleteRegion} />) }

                { this.state.isAddMode && <RegionControlPanel region={this.state.editRegion} onCancel={this.toggleAddMode} onSubmit={this.addNewRegion} /> }

                { this.state.isEditMode && <RegionControlPanel region={this.state.editRegion} onCancel={this.closeEditMode} onSubmit={this.editRegion} /> }

                { !this.state.isAddMode && !this.state.isEditMode && <RegionsControlPanel onCreate={this.toggleAddMode} onRefresh={this.getRegions} /> }

            </div>
        );
    }
}

export default Regions;