import React, {Component} from 'react';
import { Route, Routes } from 'react-router-dom';
import './App.css';
import HeaderContainer from './components/header/HeaderContainer';
import Machines from './components/machine/Machines';
import Workers from './components/worker/Workers';
import Projects from './components/project/Projects';
import UnitPrices from './components/unitPrice/UnitPrices';
import RegionsContainer from './components/region/RegionsContainer';
import DiagramContainer from './components/diagram/DiagramContainer';

class App extends Component {
  render(): React.ReactNode {
    return (
      <div className='app-wrapper'>
        <HeaderContainer />
        <div className='app-wrapper-content'>
          <Routes>
            <Route path='/diagram' element={<DiagramContainer />} />
            <Route path='/machines' element={<Machines />} />
            <Route path='/workers' element={<Workers />} />
            <Route path='/regions' element={<RegionsContainer />} />
            <Route path='/projects' element={<Projects />} />
            <Route path='/upg' element={<UnitPrices />} />
          </Routes>
        </div>
      </div>
    )
  }
}

export default App;
