import { Component, FormEvent } from "react";
import style from './Region.module.css';
import { HiBan, HiCheck } from 'react-icons/hi';
import { RegionInfo } from "../../store/types/region/RegionInfo";
import { AddRegionRequest } from "../../store/types/region/AddRegionRequest";
import { EditRegionRequest } from "../../store/types/region/EditRegionRequest";

type Props = {
    region: RegionInfo
    onCancel: () => void,
    onSubmit: (data: AddRegionRequest | EditRegionRequest) => void
}

class RegionControlPanel extends Component<Props> {
    state = this.props.region;

    onNameChange = (e: FormEvent<HTMLInputElement>) => {
        this.setState({
            name: e.currentTarget.value
        });        
    }

    onCodeChange = (e: FormEvent<HTMLInputElement>) => {
        this.setState({
            code: e.currentTarget.value
        });        
    }

    onSubmit = () => {
        this.props.onSubmit(this.state);
    }

    render() {
        return (
            <form className={style.controlPanel} >
                <div className={style.regionId} />
                <input className={style.regionNameInput} value={this.state.name} onChange={this.onNameChange} />
                <input className={style.regionCodeInput} value={this.state.code} onChange={this.onCodeChange} />
                <HiBan onClick={this.props.onCancel} />
                <HiCheck onClick={this.onSubmit} />
            </form>
        );
    }
}

export default RegionControlPanel;

