import { Component } from "react";
import style from './Region.module.css';

class RegionHeader extends Component {
    render() {
        return (
            <div className={style.regionContainer}>
                <div className={style.regionId}><b>Id</b></div>
                <div className={style.regionName}><b>Name</b></div>
                <div className={style.regionCode}><b>Code</b></div>
                <div className={style.regionButton} />
                <div className={style.regionButton} />
            </div>
        );
    }
}

export default RegionHeader;