import { Component } from "react";
import style from './Header.module.css';
import Module from "../module/Module";

class Header extends Component {
    render() {
        return (
            <header className={style.header}>
                <Module link="/diagram" name="Diagram" />
                <Module link="/machines" name="Machines" />
                <Module link="/workers" name="Workers" />
                <Module link="/regions" name="Regions" />
                <Module link="/projects" name="Projects" />
                <Module link="/upg" name="Unit Price Guide" />
            </header>
        );
    }
}

export default Header;
