import { Component } from "react";
import style from './Region.module.css';
import { HiOutlineX } from 'react-icons/hi';
import { HiPencil } from 'react-icons/hi';
import { RegionInfo } from "../../store/types/region/RegionInfo";

type Props = {
    region: RegionInfo,
    deleteRegion: (id: number) => void,
    openEditMode: (region: RegionInfo) => void,
}

class Region extends Component<Props> {
    deleteRegion = () => {
        this.props.deleteRegion(this.props.region.id);
    }

    editRegion = () => {
        this.props.openEditMode(this.props.region);
    }

    render() {
        return (
            <div className={style.regionContainer}>
                <div className={style.regionId}>{this.props.region.id}</div>
                <div className={style.regionName}>{this.props.region.name}</div>
                <div className={style.regionCode}>{this.props.region.code}</div>
                <HiPencil onClick={this.editRegion} />
                <HiOutlineX onClick={this.deleteRegion} />
            </div>
        );
    }
}

export default Region;
